.header-slider {
	margin-top: rem(40);
	margin-bottom: rem(30);

	height: rem(282);
	border-radius: rem(3);
	overflow: hidden;

	@include min768 {
		height: rem(200);
		margin-top: rem(16);
		margin-bottom: rem(40);
		overflow: visible;

		.container {
			margin-left: 0;
			margin-right: 0;
		}
	}

	.swiper-slide {
		position: relative;
		height: rem(282);

		@include min768 {
			height: rem(200);
		}

		.title {
			position: absolute;
			top: rem(73);
			left: rem(316);
			max-width: rem(266);
			font-size: rem(28);
			line-height: rem(28);
			color: white;
			font-weight: bold;

			@include min768 {
				font-size: rem(24);
				line-height: rem(24);

				top: rem(42);
				left: rem(110);
				max-width: rem(200);
			}

			@include min540 {
				font-size: rem(16);
				line-height: rem(16);
				top: rem(62);
				left: rem(80);
				max-width: rem(156);
			}

			
		}

		img {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: left;
			z-index: -1;

			@include min768 {
				display: none;
			}
		}

		.smart {
			display: none;
			position: relative;

			@include min768 {
				display: block;
			}
		}
	}


	.swiper-control {
		position: absolute;
		z-index: 10;
		right: rem(16);
		bottom: rem(16);

		@include min768 {
			position: relative;
			bottom: 0;
			right: auto;
			margin-top: rem(10);
			display: flex;
			justify-content: center;
		}

		&--nav {
			display: flex;
			align-items: center;

			&-prev {
				margin-right: rem(10);
			}

			&-next {
				margin-left: rem(10);
			}

			&-prev, &-next {
				width: rem(38);
				height: rem(38);
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				background-color: rgba(34, 34, 34, .1);
				backdrop-filter: blur(24px);

				@include min768 {
					display: none;
				}
			}

			&-page {
				.swiper-pagination-bullet {
					width: rem(12);
					height: rem(12);
					opacity: 1;
					background-color: rgba(34, 34, 34, 0.3);
					margin: 0 rem(10) 0 0;

					@include min768 {
						background-color: #DCDCDC;
					}

					&:hover {
						background-color: var(--c-orange);
					}

					&:last-child {
						margin-right: 0;
					}
				}

				.swiper-pagination-bullet-active {
					background-color: var(--c-orange);
				}
			}
		}
	}
}