.footer {

	background-color: var(--c-dark);
	padding-top: rem(90);
	padding-bottom: rem(46);

	@include min1050 {
		padding-top: rem(17);
	}

	> .container {
		display: flex;
		flex-direction: column;
	}

	&__top {
		display: flex;
		flex-wrap: wrap;

		margin-left: rem(100);

		@include min1050 {
			margin-left: 0;
		}

		&_left {
			display: flex;
			flex-direction: column;

			@include min768 {
				order: 1;
				flex: 0 0 100%;
				max-width: 100%;
				margin-top: rem(42);
			}

			.logo {
				display: block;
				margin-bottom: rem(40);

				font-weight: bold;
				font-size: rem(24);
				color: #B1B1B1;

				@include min768 {
					margin-bottom: rem(20);
				}

				

				&:hover {
					color: var(--c-orange);
				}
			}

			.social_list {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				a {

					margin-right: rem(16);

					&:hover {
						.icon-twitter path,
						.icon-wt path,
						.icon-youtube path {
							fill: var(--c-orange);
						}

						.icon-instagram path {
							stroke: var(--c-orange);
						}
					}

					&:last-child {
						margin-right: 0;
					}

					svg {
						path {
							transition: var(--transition);
						}
					}

					.icon-twitter {
						path {
							fill: #F4F4F4;
						}
					}

					.icon-instagram {
						path {
							stroke: #F4F4F4;
						}
					}

					.icon-wt {
						path {
							fill: #F4F4F4;
						}
					}

					.icon-youtube {
						path {
							fill: #F4F4F4;
						}
					}
				}
			}
		}

		&_right {
			display: flex;
			flex-wrap: wrap;

			margin-left: rem(140);

			@include min768 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-left: 0;
				order: 0;

				// justify-content: center;
			}

			.list {
				margin-right: rem(64);

				display: flex;
				flex-direction: column;

				@include min768 {
					// margin-right: rem(24);
					align-items: center;
					flex: 0 0 50%;
					max-width: 50%;
					margin-right: 0;
				}

				&:last-child {
					margin-right: 0;

					.list__bottom {
						@include min768 {
							display: none;
						}
					}
				}

				&__header {
					margin-bottom: rem(16);

					&:hover {
						b {
							color: var(--c-orange);
						}
					}

					b {
						transition: var(--transition);
						color: white;
					}
				}

				ul {
					margin-bottom: rem(16);

					li {
						margin-bottom: rem(8);

						@include min768 {
							display: none;
						}

						&:last-child {
							margin-bottom: 0;
						}

						a {
							font-size: rem(14);
							color: #B1B1B1;

							&:hover {
								color: var(--c-orange);
							}
						}
					}
				}

				&__bottom {

					align-self: baseline;

					background-color: transparent;
					border: rem(1) solid white;
					border-radius: rem(4);

					display: flex;
					align-items: center;
					justify-content: center;

					padding: rem(14) rem(28);

					@include min768 {
						padding: rem(9) rem(16);
					}

					&:hover {
						background-color: var(--c-orange);
						border-color: var(--c-orange);

						span {
							color: white;
						}
					}

					svg {
						display: none;
						margin-right: rem(8);

						@include min768 {
							display: block;
						}
					}

					span {
						transition: var(--transition);
						font-size: rem(12);
						color: #F4F4F4;

						white-space: nowrap;
					}
				}

				&__bottom.smart {
					display: none;

					@include min768 {
						display: flex;
						margin-top: rem(10);
					}
				}
			}
		}
	}

	&__bottom {
		margin-top: rem(70);


		@include min768 {
			margin-top: rem(20);
		}

		p {
			font-size: rem(12);
			line-height: rem(18);
			color: #B1B1B1;
		}
	}

}