.aside {
	flex: 0 0 rem(267);
	max-width: rem(267);
	margin-right: rem(26);
	
	margin-top: rem(30);
}

.nav_cart {
	flex: 0 0 rem(226);
	max-width: rem(226);
	margin-right: rem(24);

	margin-top: rem(76);

	@include min1200 {
		display: none;
	}

	ul {
		display: flex;
		flex-direction: column;

		li {
			margin-bottom: rem(22);

			&:last-child {
				margin-bottom: 0;
			}

			a {
				font-weight: 600;
				font-size: rem(24);

				&:hover {
					color: var(--c-orange);
				}
			}

			a.active {
				color: var(--c-orange);
			}
		}
	}
}

.filter_item {
	display: flex;
	flex-direction: column;

	padding-bottom: rem(28);
	margin-bottom: rem(26);

	border-bottom: rem(1) solid rgba(0,0,0,.5);

	&__fields {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		margin-bottom: rem(24);
	}

	&__input {
		flex: 0 0 calc(50% - 7px);
		max-width: calc(50% - 7px);
		
		display: flex;
		align-items: center;

		span {
			font-size: rem(14);
			margin-right: rem(8);
		}

		input {
			border: rem(1) solid rgba(0,0,0,.5);
			border-radius: rem(3);
			height: rem(22);
			width: 100%;
			padding: 0 rem(6);
		}
	}

	.title {
		font-size: rem(14);
		line-height: rem(22);
		margin-bottom: rem(8);
	}

	.field-checked {
		margin-bottom: rem(3);

		&:last-child {
			margin-bottom: 0;
		}
	}

	#uirange {
		margin-bottom: rem(9);
	}
}