:root {
  --container: 1185px;
  --c-orange: #FE9C00;
  --c-orange-light: #FFE381;
  --c-dark-gray: #323232;
  --c-dark: #222222;
  --c-gray-light: #F3F3F3;
  --c-gray: #8D8C8C;
  --c-green: #52B636;
  --c-blue: #096193;
  --size-default: 14px;
  --f-default: 'Montserrat';
  --transition: 250ms ease;
}

* {
  outline: none;
  border: 0;
}

html, body {
  position: relative;
  overflow-x: hidden;
}

html {
  font-size: 100%;
}

button {
  background-color: transparent;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

button, a {
  transition: var(--transition);
}

li {
  list-style: none;
}

body {
  position: relative;
  font-size: 1rem;
  font-family: var(--f-default);
  color: var(--c-dark);
  overflow-x: hidden;
}

.container {
  max-width: var(--container);
  margin: 0 auto;
}

@media (max-width: 1200px) {
  .container {
    max-width: 100%;
    margin-left: 20px;
    margin-right: 20px;
  }
}

.relative {
  position: relative;
}

