.field-checked {
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.field-checked:hover .text {
  color: var(--c-orange);
}

.field-checked input {
  display: none;
}

.field-checked input:checked ~ .check::before {
  opacity: 1;
}

.field-checked .check {
  margin-top: 0.1875rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 0.875rem;
  width: 0.875rem;
  height: 0.875rem;
  background-color: white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-right: 0.5rem;
}

.field-checked .check::before {
  content: "";
  width: 0.5rem;
  height: 0.5rem;
  background-color: var(--c-orange);
  transition: var(--transition);
  opacity: 0;
}

.field-checked .text {
  transition: var(--transition);
  font-size: 0.875rem;
}

.field-checked.radio input:checked ~ .check {
  border-color: var(--c-orange);
}

@media (max-width: 540px) {
  .field-checked.radio input:checked ~ .check {
    background-color: var(--c-orange);
  }
}

.field-checked.radio .check {
  border-radius: 50%;
  box-shadow: none;
  border: 0.0625rem solid #AEAEAE;
}

@media (max-width: 540px) {
  .field-checked.radio .check {
    width: 1.5rem;
    height: 1.5rem;
    flex: 0 0 1.5rem;
    margin-top: 0;
  }
}

.field-checked.radio .check::before {
  border-radius: 50%;
}

@media (max-width: 540px) {
  .field-checked.radio .check::before {
    width: 1.125rem;
    height: 1.125rem;
    border: 0.125rem solid white;
  }
}

@media (max-width: 540px) {
  .field-checked.radio .text {
    margin-top: 0.1875rem;
  }
}

