.smart_cart {
	position: fixed;

	bottom: rem(28);
	right: rem(20);

	width: rem(60);
	height: rem(60);
	border-radius: 50%;
	background-color: var(--c-orange);

	display: flex;
	align-items: center;
	justify-content: center;

	box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.15);

	z-index: 99;

	@include max540 {
		display: none;
	}

	svg {
		margin-bottom: rem(5);
	}

	span {
		color: white;
		font-size: rem(12);
		font-weight: 600;

		position: absolute;
		top: 50%;
		left: 50%;

		margin-top: rem(-6.5);
		margin-left: rem(-3);
	}
}