.modal {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.35);
	z-index: 100;

	

	display: none;

	align-items: center;
	justify-content: center;
	

	&__wrapper {
		position: relative;
		background-color: white;
		overflow-y: auto;
		width: 100%;
		height: 100%;

		@include max540 {
			width: rem(400);
			height: auto;
			border-radius: rem(8);
		}
	}

	&__header {
		position: relative;
		height: rem(50);
		display: flex;
		align-items: center;
		justify-content: center;

		border-bottom: rem(1) solid #B1B1B1;

		padding-left: 20px;
		padding-right: 20px;

		margin-bottom: rem(20);

		.modal__close {
			position: absolute;
			top: rem(13);
			right: rem(15);
		}

		a {
			width: rem(30);
			height: rem(30);

			display: flex;
			align-items: center;
			justify-content: center;
		}

		.title {
			font-size: rem(18);
			font-weight: 600;
		}
	}

	&__content {
		display: flex;
		flex-direction: column;

		padding-bottom: rem(28);

		&_form {
			display: flex;
			flex-direction: column;
		}

		&_sity {

			display: flex;
			flex-direction: column;

			margin-left: 20px;
			margin-right: 20px;

			.title {
				font-size: rem(24);
				font-weight: 600;
				text-align: center;

				margin-top: rem(64);
				margin-bottom: rem(120);

				span {
					color: var(--c-orange);
				}
			}

			.buttons {
				display: flex;
				flex-direction: column;

				.button-orange,
				.button-border {
					height: rem(48);

					span {
						font-size: rem(16);
						font-weight: bold;
					}

				}

				.button-orange {
					margin-bottom: rem(8);
				}
			}
		}

		&_auth {
			margin-left: 20px;
			margin-right: 20px;

			.title {
				font-size: rem(18);
				font-weight: bold;
				margin-bottom: rem(16);
			}

			.field {
				width: 100%;
				height: rem(48);
				border: 0;
				background-color: #F4F4F4;

				padding: 0 rem(15);

				display: flex;
				align-items: center;

				margin-bottom: rem(10);

				@include placeholder {
					color: var(--c-dark);
				}
			}

			.filter_checked {
				align-items: flex-start;
				margin-bottom: rem(20);

				.title {
					margin-bottom: 0;
					a {
						margin-bottom: 0;
					}
				}
			}

			a {
				margin-bottom: rem(20);
				font-size: rem(14);
				color: var(--c-orange);
				font-weight: 600;
				display: block;

				&:hover {
					text-decoration: underline;
				}
			}

			.buttons {
				display: flex;
				flex-direction: column;

				.button-orange,
				.button-border {
					height: rem(48);

					span {
						font-size: rem(16);
						font-weight: bold;
					}

				}

				.button-orange {
					margin-bottom: rem(8);
				}
			}
		}

		&_search {
			margin-left: 20px;
			margin-right: 20px;
			.field {
				width: 100%;
				height: rem(48);
				border: 0;
				background-color: #F4F4F4;

				padding: 0 rem(15);

				display: flex;
				align-items: center;

				margin-bottom: rem(10);

				@include placeholder {
					color: var(--c-dark);
				}
			}

			.buttons {
				display: flex;
				flex-direction: column;

				.button-orange,
				.button-border {
					height: rem(48);

					span {
						font-size: rem(16);
						font-weight: bold;
					}

				}

				.button-orange {
					margin-bottom: rem(8);
				}
			}
		}

		.filter_button {
			margin-left: 20px;
			margin-right: 20px;

			background-color: var(--c-orange);
			height: rem(48);
			border-radius: rem(4);

			display: flex;
			justify-content: center;
			align-items: center;

			color: white;
			font-weight: bold;
		}
	}

	&__filter_item {
		display: flex;
		flex-direction: column;

		border-bottom: rem(0.5) solid #B1B1B1;
		padding-bottom: rem(20);
		padding-left: 20px;
		padding-right: 20px;

		margin-bottom: rem(20);

		&:last-child {
			margin-bottom: 0;
			border-bottom: 0;
		}

		> .title {
			font-size: rem(18);
			line-height: rem(22);
			font-weight: bold;
			margin-bottom: rem(16);
		}

		.filter_checked {
			margin-bottom: rem(14);

			&:last-child {
				margin-bottom: 0;
			}
		}


	}

	&__filter_item.last {
		border-bottom: 0;
	}
}

.modal.active {
	display: flex;
}

.filter_reset {
	color: #F82929;
	font-weight: 500;
	align-self: flex-start;
	margin-bottom: rem(28);
}

.filter_fields {
	display: flex;
	justify-content: space-between;
	margin-bottom: rem(20);

	.filter_field {
		flex: 0 0 calc(50% - 12px);
		max-width: calc(50% - 12px);
	}
}

.filter_range {
	height: rem(8) !important;
	background-color: #F4F4F4 !important;

	margin-bottom: rem(20);

	.noUi-base {

	}

	.noUi-handle {
		width: rem(30) !important;
		height: rem(30) !important;
		top: 13px !important;
		right: rem(-15) !important;
	}

	.noUi-touch-area {
		position: relative;
		background: var(--c-orange) none !important;
		width: rem(30);
		height: rem(30);
		border-radius: 50%;

		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: "";
			width: rem(14);
			height: rem(14);
			border-radius: 50%;
			background-color: white;
		}

		&::after {
			position: absolute;
			top: rem(-15);
			left: 50%;
			margin-left: rem(-1);
			content: "";
			z-index: -1;
			height: rem(30);
			width: rem(2);
			border-radius: rem(2);
			background-color: var(--c-orange);
		}
	}
}

.filter_field {
	position: relative;

	input {
		background-color: #F4F4F4;
		border-radius: rem(4);
		width: 100%;
		height: rem(40);
		color: #B1B1B1;
		font-weight: 500;
		font-size: rem(16);
		padding: 0 rem(40);

		appearance: none;

		@include placeholder {
			color: #B1B1B1;
		}
	}

	span {
		position: absolute;
		font-weight: 500;
		color: #B1B1B1;

		&:first-of-type {
			top: rem(12);
			left: rem(9);
		}

		&:last-of-type {
			top: rem(12);
			right: rem(9);
		}
	}
}

.filter_checked {
	display: flex;
	align-items: center;

	input {
		display: none;

		&:checked {
			~ .check {
				background-color: var(--c-orange);
				border-color: var(--c-orange);

				svg {
					opacity: 1;
				}
			}


		}
	}

	.check {
		width: rem(24);
		height: rem(24);
		border-radius: rem(4);
		margin-right: rem(10);
		display: flex;
		align-items: center;
		justify-content: center;
		border: rem(1) solid #B1B1B1;

		svg {
			opacity: 0;
		}
	}

	.title {
		font-size: rem(14);
		line-height: rem(22);
		font-weight: 500;
	}

}

#modal_filter {
	@include max540 {
		display: none;
	}
}

#modal_login, #modal_register {
	.modal__header {
		@include max540 {
			height: 0;
			border-bottom: 0;
		}
	}
}