.header {

	display: flex;
	flex-direction: column;

	padding-bottom: rem(4);
	border-bottom: rem(1) solid #B1B1B1;

	&__top {
		width: 100%;
		background-color: #F4F4F4;
		height: rem(40);
		margin-bottom: rem(14);

		@include min {
			margin-bottom: rem(10);
		}

		> .container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
			height: 100%;
		}

		&-logo {
			display: none;

			@include min {
				display: block;
			}
		}

		&-change {
			height: 100%;
			button {
				background-color: var(--c-dark);
				padding: 0 rem(12);
				display: flex;
				align-items: center;
				height: 100%;

				@include min {
					background-color: transparent;
					flex-direction: row-reverse;
				}

				.smart {
					display: none;

					@include min {
						display: block;
					}
				}

				.desktop {
					@include min {
						display: none;
					}
				}

				svg {
					path {
						fill: white;

						@include min {
							fill: var(--c-dark);
						}
					}
				}

				span {
					margin-left: rem(5);
					color: white;
					font-size: rem(12);
					font-weight: 500;

					@include min {
						color: var(--c-dark);
						margin-left: 0;
						margin-right: rem(5);
					}
				}
			}
		}

		&-nav {
			@include min {
				display: none;
			}
			ul {
				display: flex;
				flex-wrap: wrap;
				

				li {
					margin-right: rem(70);

					@include min1050 {
						margin-right: rem(35);
					}

					&:last-child {
						margin-right: 0;
					}

					a {
						font-size: rem(12);

						&:hover {
							color: var(--c-orange);
						}
					}
				}
			}
		}
	}

	&__control {

		margin-bottom: rem(20);

		@include min {
			margin-bottom: rem(12);
		}

		@include min540 {
			margin-bottom: rem(4);
		}

		> .container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			@include min {
				justify-content: space-between;
			}
		}

		&-prev {
			display: none;
		}

		&-burger {
			width: rem(16);
			height: rem(12);

			display: none;
			flex-direction: column;
			justify-content: space-between;

			@include min {
				display: flex;
			}

			.line {
				width: 100%;
				height: rem(2);
				background-color: var(--c-dark);
				border-radius: rem(1);
			}
		}

		&-logo {
			margin-right: rem(46);

			@include min {
				display: none;
			}

			a {
				display: block;
			}
		}

		&-search {
			position: relative;

			flex: 1;

			@include min {
				display: none;
			}

			.search-field {
				width: 100%;
				height: rem(46);
				border-radius: rem(47);
				border: rem(1) solid var(--c-orange);
				padding-left: rem(14);
				padding-right: rem(42);
				font-size: rem(14);
				color: #ACACAC;

				@include placeholder {
					color: #ACACAC;
				}
			}

			.search-button {
				position: absolute;
				top: rem(10);
				right: rem(11);

				&:hover {
					svg {
						rect {
							stroke: var(--c-orange);

							&:last-child {
								fill: var(--c-orange);
							}
						}
					}
				}

				svg {
					rect {
						transition: var(--transition);
						stroke: var(--c-dark);
						
						&:last-child {
							fill: var(--c-dark);
						}
					}
				}
			}
		}

		&-user {
			margin-left: rem(60);

			display: flex;
			flex-wrap: wrap;
			align-items: center;

			

			.link {
				display: flex;
				flex-direction: column;
				align-items: center;

				margin-right: rem(60);

				@include min {
					margin-right: 0;

					&:first-child {
						margin-right: rem(25);
					}
				}

				&:last-child {
					margin-right: 0;

					@include min {
						display: none;
					}
				}

				&:hover {
					svg {
						path, rect {
							stroke: var(--c-orange);
						}
					}
					span {
						color: var(--c-orange);
					}
				}

				svg {
					path, rect {
						transition: var(--transition);
						stroke: var(--c-dark);
					}
				}

				span {
					transition: var(--transition);
					font-size: rem(10);
					font-weight: 600;

					@include min {
						display: none;
					}
				}
			}

			.smart {
				display: none;

				@include min {
					display: block;
				}
			}
		}
	}

	&__nav {

		@include min {
			display: none;
		}

		.container > ul {
			display: flex;
			flex-wrap: wrap;
			margin-left: rem(-9);
			margin-right: rem(-9);

			> li {
				position: relative;
				margin-right: rem(25);
				// margin-bottom: rem(16);
				&:last-child {
					margin-right: 0;
				}

				> a {
					display: flex;
					align-items: center;

					font-weight: 500;
					height: rem(40);
					padding: 0 rem(8);

					border-radius: rem(4);

					@include min {
						font-size: rem(14);
						height: rem(32);
						padding: 0 rem(5);
					}

					&:hover {
						color: var(--c-orange);

						
					}
				}
				
			}

			.header__nav-drop {

				&:hover {

					a {
						color: var(--c-dark);
						background-color: #EEEEEE;

						border-bottom-left-radius: 0;
						border-bottom-right-radius: 0;
					}

					.header__nav-drop-panel {
						display: flex;
					}
				}

				a {
					&:hover {
						color: var(--c-dark);
						background-color: #EEEEEE;

						border-bottom-left-radius: 0;
						border-bottom-right-radius: 0;
					}
				}

				&-panel {
					position: absolute;
					z-index: 10;
					left: 0;
					top: 100%;
					width: 1203px;
					background-color: #EEEEEE;
					border-radius: rem(4);

					border-top-left-radius: 0;
					
					padding: rem(26) rem(50);

					// display: flex;
					flex-wrap: wrap;
					justify-content: space-between;

					display: none;

					ul {
						display: flex;
						flex-direction: column;

						li {
							margin-bottom: rem(14);

							&:last-child {
								margin-bottom: 0;
							}

							span {
								font-weight: bold;
							}

							a {
								font-weight: 500;

								&:hover {
									color: var(--c-orange);
								}
							}
						}
					}
				}
			}
		}
	}

}