.button-orange {
	// width: 100%;
	padding: 0 rem(14);
	height: rem(40);

	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--c-orange);

	border-radius: rem(20);

	@include min540 {
		height: rem(20);
		border-radius: rem(3);
		padding: 0 rem(6);
	}

	svg {
		margin-right: rem(8);

		@include min540 {
			display: none;
		}
	}

	span {
		transition: var(--transition);
		color: white;
		font-size: rem(14);
		font-weight: 500;

		@include min540 {
			font-size: rem(8);
		}
	}

	&:hover {
		opacity: .9;
	}
}

.button-border {
	// width: 100%;
	padding: 0 rem(14);
	height: rem(40);

	display: flex;
	justify-content: center;
	align-items: center;
	background-color: transparent;

	border: rem(2) solid var(--c-orange);

	border-radius: rem(20);

	@include min540 {
		height: rem(20);
		border-radius: rem(3);
		padding: 0 rem(6);
	}

	svg {
		margin-right: rem(8);

		@include min540 {
			display: none;
		}
	}

	span {
		transition: var(--transition);
		color: var(--c-orange);
		font-size: rem(14);
		font-weight: 500;

		@include min540 {
			font-size: rem(8);
		}
	}

	&:hover {
		opacity: .9;
	}
}

.button-orange-disabled {
	cursor: default;
	pointer-events: none;
	background-color: var(--c-orange-light);

	span {
		color: var(--c-dark);
		font-weight: normal;
	}
}


