.field-checked {
	cursor: pointer;
	display: flex;
	flex-wrap: wrap;
	align-items: flex-start;

	&:hover {
		.text {
			color: var(--c-orange);
		}
	}

	input {
		display: none;

		&:checked {
			~.check {
				&::before {
					opacity: 1;
				}
			}
		}
	}

	.check {
		margin-top: rem(3);
		display: flex;
		align-items: center;
		justify-content: center;

		flex: 0 0 rem(14);

		width: rem(14);
		height: rem(14);
		background-color: white;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
		margin-right: rem(8);

		

		&::before {
			content: "";
			width: rem(8);
			height: rem(8);
			background-color: var(--c-orange);

			transition: var(--transition);
			opacity: 0;
		}
	}

	.text {
		transition: var(--transition);
		font-size: rem(14);
	}
}

.field-checked.radio {

	input {
		&:checked {
			~ .check {
				border-color: var(--c-orange);
				
				@include min540 {
					background-color: var(--c-orange);
				}
			}
		}
	}

	.check {
		border-radius: 50%;
		box-shadow: none;
		border: rem(1) solid #AEAEAE;

		@include min540 {
			width: rem(24);
			height: rem(24);
			flex: 0 0 rem(24);
			margin-top: 0;
		}

		&::before {
			border-radius: 50%;

			@include min540 {
				width: rem(18);
				height: rem(18);

				border: rem(2) solid white;
			}
		}
	}

	.text {
		@include min540 {
			margin-top: rem(3);
		}
	}
}