.page_pay {
	margin-left: auto;
	margin-right: auto;

	max-width: rem(900);

	@include min768 {
		max-width: 100%;
	}

	.page_cart__table {
		max-width: rem(830);
		margin-left: auto;
		margin-right: auto;

		@include min768 {
			max-width: 100%;
		}
	}

	.page_cart__content {
		padding-left: 0;
	}

	.title {
		margin-left: rem(84);

		@include min768 {
			margin-left: 0;
		}
	}

	.bottom__item {
		@include min540 {
			position: relative;
			padding-bottom: rem(20);
			
			&::before {
				content: "";
				position: absolute;
				left: -20px;
				bottom: 0;
				width: calc(100% + 40px);
				height: rem(1);
				border-bottom: rem(0.5) solid #B1B1B1;
			}


			&:nth-child(3) {
				padding-bottom: 0;
				
				&::before {
					display: none;
				}
			}
		}
		
	}

	.page_cart__bottom .bottom {
		@include min768 {
			.flex {
				order: 3;
				margin-top: rem(28);

				.button-orange {
					margin-top: 0;
					display: flex;
					background-color: #AAAAAA;
				}
			}
		}
	}
}

.page_cart {
	flex: 1;

	display: flex;
	flex-direction: column;

	.title {
		font-size: rem(24);
		margin-bottom: rem(24);
	}

	&__content {
		padding-left: rem(70);

		@include min1200 {
			padding-left: 0;
		}
	}

	&__table {
		display: flex;
		flex-direction: column;

		&_row {
			display: flex;
			flex-wrap: wrap;

			padding: rem(16) 0;

			border-bottom: rem(1) solid rgba(0,0,0,.5);

			@include min {
				justify-content: space-between;
			}

			@include min768 {
				position: relative;

				padding: rem(10);
				border: rem(0.6) solid #DCDCDC;
				border-radius: rem(3);
				// border-bottom: 0;

				margin-bottom: rem(10);
			}

			&:first-child {
				padding-top: 0;

				@include min660 {
					display: none;
				}
				
			}

			&:last-child {
				border-bottom: 0;

				@include min768 {
					border-bottom: rem(0.6) solid #DCDCDC;
				}
			}

			.col {
				margin-right: rem(14);

				font-size: rem(14);

				@include min768 {
					margin-right: 0;
				}

				&:last-child {
					margin-right: 0;
				}

				.image {
					display: flex;
					align-items: center;
					justify-content: center;
				}

				ul {
					line-height: rem(22);

					li {
						&:nth-child(2) {
							@include min768 {
								display: none;
							}
						}

						&:last-child {
							@include min768 {
								margin-top: rem(30);

								b {
									font-size: rem(18);
									font-weight: 500;
								}
							}
						}
					}
				}
			}

			.col-1 {
				flex: 0 0 rem(60);
				max-width: rem(60);

				@include min768 {
					display: none;
				}
			}

			.col-2 {
				flex: 0 0 rem(85);
				max-width: rem(85);
				text-align: center;

				@include min768 {
					order: 0;

					flex: 0 0 45%;
					max-width: 45%;
					margin-bottom: rem(10);
				}
			}

			.col-3 {
				flex: 0 0 rem(220);
				max-width: rem(220);

				@include min {
					flex: 0 0 rem(140);
					max-width: rem(140);
				}

				@include min768 {
					order: 1;

					flex: 0 0 45%;
					max-width: 45%;

					margin-right: auto;
					
				}
			}

			.col-4 {
				flex: 0 0 rem(145);
				max-width: rem(145);

				text-align: right;

				@include min {
					flex: 0 0 rem(75);
					max-width: rem(75);
				}

				@include min768 {
					order: 3;
					display: none;
				}

				b {
					display: block;
					margin-top: rem(6);
				}
			}

			.col-5 {
				flex: 0 0 rem(100);
				max-width: rem(100);

				@include min768 {
					order: 2;
				}
			}

			.col-6 {
				flex: 0 0 rem(100);
				max-width: rem(100);

				@include min768 {
					order: 4;
					text-align: right;
				}

				b {
					display: block;
					margin-top: rem(6);

					@include min768 {
						font-size: rem(18);
					}
				}
			}
		}
	}

	&__bottom {
		display: flex;
		flex-direction: column;

		.top {
			align-self: flex-end;

			width: rem(260);

			display: flex;
			flex-direction: column;
			margin-right: rem(100);

			@include min768 {
				margin-right: 0;
				width: 100%;
				margin-top: rem(30);
			}

			.first {
				margin-bottom: rem(10);

			}

			b {
				font-size: rem(18);
			}
		}

		.bottom {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			margin-top: rem(40);

			.button-orange {
				background-color: var(--c-orange);
				color: white;
				margin-top: rem(40);
			}

			&__item {
				display: flex;
				flex-direction: column;

				flex: 0 0 33.333%;
				max-width: 33.333%;

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-bottom: rem(20);

					&:first-child {
						order: 1;
					}

					&:last-child {
						margin-bottom: 0;

						order: 0;
					}

					&:nth-child(2) {
						order: 2;
					}
				}

				b {
					margin-bottom: rem(10);
				}
				.field-checked {
					flex-wrap: nowrap;
					margin-bottom: rem(10);

					&:last-child {
						margin-bottom: 0;
					}
				}

				.field {
					width: rem(216);
					border-radius: rem(3);
					border: rem(1) solid #626262;
					height: rem(32);

					margin-bottom: rem(8);

					padding: 0 rem(10);

					transition: var(--transition);

					@include min540 {
						width: 100%;
						height: rem(48);
						border: 0;
						background-color: #F4F4F4;
					}

					&:focus {
						border-color: var(--c-orange);
					}

					&:last-child {
						margin-bottom: 0;
					}

					@include placeholder {
						color: var(--c-dark);
					}
				}
			}

			a {
				width: rem(234);
				height: rem(40);
				border-radius: rem(3);
				
				display: flex;
				justify-content: center;
				align-items: center;

				background-color: #FFE381;
				font-size: rem(14);
				font-weight: 600;

				@include min768 {
					&:first-child {
						display: none;
					}

					width: 100%;

					background-color: var(--c-orange);
					height: rem(48);
					color: white;
					font-weight: bold;
				}

				&:hover {
					background-color: var(--c-orange);
				}
			}
		}
	}
}


.number {
	display: flex;
	height: rem(30);
	border-radius: rem(3);

	button {
		flex: 0 0 rem(30);
		width: rem(30);
		height: rem(30);
		border: rem(1) solid #C0C0C0;

		color: #C0C0C0;
		font-weight: bold;
		font-size: rem(20);

		&:hover {
			color: var(--c-orange);
		}
	}

	input {
		width: 100%;
		text-align: center;
		font-weight: bold;

		border-top: rem(1) solid #C0C0C0;
		border-bottom: rem(1) solid #C0C0C0;

		@include placeholder {
			color: var(--c-dark);
		}
	}
}

