.section_blocks {
	margin-bottom: rem(30);

	@include min540 {
		margin-bottom: rem(20);
	}

	&__header {
		margin-bottom: rem(20);

		@include min540 {
			margin-bottom: rem(14);
		}

		.title {
			font-size: rem(24);
			font-weight: bold;

			@include min540 {
				font-size: rem(16);
			}
		}
	}

	&__content {
		.swiper-scrollbar {
			position: relative !important;
			top: auto !important;
			left: auto !important;
			bottom: auto !important;
			margin-top: rem(10);

			&-drag {
				background-color: var(--c-orange) !important;
			}
		}
	}
}