.breadcrumbs {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	.sep {
		margin-left: rem(8);
		margin-right: rem(8);

		display: flex;
		align-items: center;
	}

	a {
		font-size: rem(14);
		color: rgba(0, 0, 0, .55);
		text-decoration: underline;

		&:hover {
			color: var(--c-orange);
		}
	}
}