.catalog {
	flex: 1;
	display: flex;
	flex-direction: column;

	.breadcrumbs {
		margin-bottom: rem(20);

		@include min540 {
			display: none;
		}
	}


	&__page {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		justify-content: center;

		> a {
			width: rem(40);
			height: rem(40);

			background-color: #F4F4F4;
			display: flex;
			align-items: center;
			justify-content: center;

			border-radius: rem(6);

			@include min350 {
				width: rem(28);
				height: rem(28);
			}

			svg {
				path {
					transition: var(--transition);
					stroke: #B1B1B1;
				}
			}

			&:hover {
				svg {
					path {
						stroke: var(--c-orange);
					}
				}
			}
		}

		&-first,
		&-prev {
			margin-right: rem(6);
		}

		&-last,
		&-next {
			margin-left: rem(6);
		}

		ul {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			height: rem(40);
			border-radius: rem(6);
			background-color: #F4F4F4;
			padding: 0 rem(14);

			@include min350 {
				height: rem(28);
			}

			li {
				
				display: flex;
				justify-content: center;
				align-items: center;
				margin-right: rem(9);

				&:last-child {
					margin-right: 0;
				}
			}

			a {
				width: rem(20);
				height: rem(20);
				display: flex;
				align-items: center;
				justify-content: center;
				font-weight: 500;

				&:hover {
					color: var(--c-orange);
				}
			}
		}
		
	}


	&__smart {
		display: none;
		flex-direction: column;

		margin-bottom: rem(18);

		@include min540 {
			display: flex;
		}

		.title {
			font-size: rem(24);
			margin-bottom: rem(16);
		}

		b {
			font-size: rem(18);
			margin-bottom: rem(20);
		}

		.catalog_button_filter {
			width: 100%;
			height: rem(50);

			display: flex;
			align-items: center;
			justify-content: center;
			background-color: #F4F4F4;

			font-weight: 600;
		}
	}


	&__items {
		display: flex;
		flex-wrap: wrap;

		@include min540 {
			align-self: baseline;
		}

		.product_item {
			width: calc((100% - 32px) / 3);
			margin-right: rem(16);
			margin-bottom: rem(20);

			@include mw900 {
				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			

			@include min {
				width: calc((100% - 16px) / 2);

				&:nth-child(2n) {
					margin-right: 0;
				}

				
			}

			@include min540 {
				display: flex;
				flex-wrap: wrap;
				flex-direction: row;
				justify-content: space-between;

				width: 100%;
				margin-right: 0;

				margin-bottom: rem(10);

				padding: rem(10) rem(32);

				&__image {
					flex: 0 0 40%;
					max-width: 40%;
				}

				&__content {
					flex: 0 0 55%;
					max-width: 55%;
				}
			}
		}
	}

}

.page-catalog {
	.aside {
		

		@include min1050 {
			display: none;
		}
	}
}