.product_article {
	margin-top: rem(50);

	@include min768 {
		margin-top: rem(30);
	}

	> .container {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	.breadcrumbs {
		@include min768 {
			display: none;
		}
	}

	&__header {
		flex: 0 0 100%;
		max-width: 100%;
		margin-bottom: rem(32);

		@include min768 {
			margin-bottom: rem(16);
			order: 1;
		}

		.title {
			font-size: rem(24);
			font-weight: bold;
			color: black;

			margin-bottom: rem(8);

			@include min768 {
				font-size: rem(16);
				margin-bottom: 0;
			}
		}

		
	}

	&__images {
		flex: 0 0 rem(484);
		max-width: rem(484);
		margin-right: rem(12);

		height: rem(456);

		display: flex;
		flex-wrap: wrap;

		@include min1200 {
			flex: 0 0 rem(365);
			max-width: rem(365);
		}

		@include min1050 {
			flex: 0 0 rem(484);
			max-width: rem(484);
		}

		@include min {
			flex: 0 0 rem(380);
			max-width: rem(380);
		}

		@include min768 {
			flex: 0 0 100%;
			max-width: 100%;
			justify-content: center;

			height: rem(212);

			order: 0;

			margin-bottom: rem(16);
		}

		&_thumbs {
			flex: 0 0 rem(84);
			max-width: rem(84);
			margin-right: rem(16);

			@include min1200 {
				margin-right: rem(8);
			}

			@include min768 {
				display: none;
			}

			.nav-prev,
			.nav-next {
				width: 100%;

				&:hover {
					svg {
						path {
							stroke: var(--c-orange);
						}
					}
				}

				svg {
					path {
						stroke: black;
					}
				}
			}
			.nav-prev {
				margin-bottom: rem(8);
			}
			.nav-next {
				margin-top: rem(8);
			}

			.swiper-slide {
				width: rem(84);
				height: rem(89);
				border: rem(1) solid #D1D1D1;
				display: flex;
				align-items: center;
				justify-content: center;

				margin-bottom: rem(16);

				&:last-child {
					margin-bottom: 0;
				}
			}

			.swiper-slide-thumb-active {
				border-color: var(--c-orange);
			}
		}

		&_main {
			flex: 0 0 rem(384);
			max-width: rem(384);

			@include min1200 {
				flex: 0 0 rem(255);
				max-width: rem(255);
			}

			@include min1050 {
				flex: 0 0 rem(384);
				max-width: rem(384);
			}

			@include min {
				flex: 0 0 rem(280);
				max-width: rem(280);
			}

			.swiper-container {
				height: rem(458);
				border: rem(0.5) solid #D1D1D1;

				@include min768 {
					border: 0;
					height: rem(212);
				}
			}

			.swiper-slide {
				height: rem(458);
				// border: rem(0.5) solid #D1D1D1;

				display: flex;
				justify-content: center;
				align-items: center;

				@include min768 {
					height: rem(212);

					img {
						height: 100%;
					}
				}
			}
		}
	}

	&__table {
		flex: 0 0 rem(384);
		max-width: rem(384);
		display: flex;
		flex-direction: column;
		background-color: #FFFBFB;
		padding: rem(10) rem(20);

		margin-right: rem(14);

		@include min1200 {
			flex: 0 0 rem(320);
			max-width: rem(320);
		}

		@include min768 {
			order: 2;

			flex: 0 0 100%;
			max-width: 100%;

			margin-right: 0;

			padding: 0;
			background-color: transparent;
		}

		.title {
			margin-bottom: rem(14);

			@include min768 {
				margin-bottom: rem(20);
			}
		}

		b {
			margin-bottom: rem(14);

			@include min768 {
				margin-bottom: rem(20);
			}
		}

		ul {
			display: flex;
			flex-direction: column;
			margin-bottom: rem(14);

			li {
				position: relative;
				display: flex;
				flex-wrap: wrap;
				align-items: flex-end;
				justify-content: space-between;

				margin-bottom: rem(12);

				@include min1200 {
					font-size: rem(14);
				}

				@include min768 {
					margin-bottom: rem(10);
				}

				&::before {
					content: "";
					position: absolute;
					left: 0;
					bottom: rem(3);
					width: 100%;
					border-bottom: rem(0.5) dotted rgba(0, 0, 0, .37);

					@include min768 {
						display: none;
					}
				}

				span {
					position: relative;
					z-index: 1;
					background-color: #FFFBFB;

					&:first-child {
						padding-right: rem(3);

						@include min768 {
							color: #ACACAC;
						}
					}

					&:last-child {
						padding-left: rem(3);

						
					}
				}

				

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		a {
			align-self: flex-start;

			color: var(--c-blue);
			font-weight: bold;
			text-decoration: underline;

			@include min768 {
				display: none;
			}

			&:hover {
				color: var(--c-orange);
			}
		}
	}

	&__right {
		flex: 0 0 rem(284);
		max-width: rem(284);
		border: rem(1) solid #D1D1D1;
		padding: rem(32) rem(20);
		border-radius: rem(3);

		display: flex;
		flex-direction: column;

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-top: rem(24);

			flex-direction: row;
			flex-wrap: wrap;

			// align-items: baseline;
		}

		@include min768 {
			order: 3;
			flex-direction: column;

			padding: 0;
			border: 0;
		}

		.block_smart {
			display: flex;
			flex-wrap: wrap;

			@include min1050 {
				&:first-child {
					align-items: center;
					flex: 0 0 100%;
					max-width: 100%;

					margin-bottom: rem(20);
				}

				&:nth-child(2), &:nth-child(3) {
					flex: 0 0 100%;
					max-width: 100%;
				}
			}

			@include min768 {
				// background-color: #E5E5E5;
				padding: rem(10);
				border-radius: rem(4);
				border: rem(0.6) solid #DCDCDC;
				margin-bottom: rem(10) !important;

				.i_text_item {
					&__image {
						display: none;
					}
				}

				&:first-child {
					order: 0;
				}

				&:nth-child(2) {
					order: 2;
				}

				&:last-child {
					margin-bottom: 0;
					order: 1;
				}
			}

			
		}

		.price {
			font-size: rem(40);
			font-weight: bold;
			color: #FE6B00;
			margin-bottom: rem(18);

			@include min1050 {
				white-space: nowrap;

				flex: 0 0 33.333%;
				max-width: 33.333%;

				margin-bottom: 0;
			}

			@include min768 {
				flex: 0 0 100%;
				max-width: 100%;

				font-size: rem(24);
				color: #222222;
				margin-bottom: rem(6);
			}
		}

		.block_checked {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			margin-bottom: rem(17);

			@include min1050 {
				flex: 0 0 33.333%;
				max-width: 33.333%;

				margin-bottom: 0;
			}

			@include min768 {
				flex: 0 0 100%;
				max-width: 100%;

				margin-bottom: rem(14);

				svg {
					display: none;
				}
			}

			span {
				margin-left: rem(7);

				@include min768 {
					font-weight: bold;
					color: #ACACAC;
					margin-left: 0;
				}
			}
		}

		.add_cart {

			align-self: flex-start;

			padding: rem(20) rem(32);
			background-color: var(--c-orange);
			border-radius: rem(25);
			display: flex;
			align-items: center;

			margin-bottom: rem(24);

			@include min1050 {
				flex: 0 0 33.333%;
				max-width: 33.333%;
				margin-bottom: 0;
			}

			@include min {
				padding: rem(20) rem(16);
			}

			@include min768 {
				flex: 0 0 100%;
				max-width: 100%;

				padding: rem(14) 0;
				width: 100%;
				justify-content: center;

				border-radius: rem(4);

				

				svg {
					display: none;
				}
			}

			&:hover {
				opacity: .8;
			}

			span {
				margin-left: rem(8);
				color: white;
				font-size: rem(14);

				@include min768 {
					font-size: rem(16);
					font-weight: bold;
				}
			}
		}
		
		.brand_item {
			margin-bottom: rem(24);

			@include min1050 {
				flex: 0 0 100%;
				max-width: 100%;
			}

			@include min768 {
				margin-bottom: 0;
			}
		}
	}

	&__content {
		flex: 0 0 100%;
		max-width: 100%;
		margin-top: rem(54);
		margin-bottom: rem(16);

		@include min768 {
			order: 4;

			margin-top: rem(16);
		}

		.show-text {
			display: none;

			@include min768 {
				margin-top: rem(10);

				color: var(--c-orange);
				font-size: rem(14);
			}
		}

		.title {
			font-size: rem(16);
			padding-bottom: rem(4);
			border-bottom: rem(2) solid #FE6B00;
			display: inline-block;

			margin-bottom: rem(16);

			@include min768 {
				font-size: rem(14);
				border-bottom: 0;
				margin-bottom: rem(10);
			}
		}

		p {
			line-height: rem(26);

			margin-bottom: rem(28);

			@include min768 {
				margin-bottom: rem(10);
				font-size: rem(14);
				line-height: 140%;

			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	&__content.hide {
		.show-text {
			display: block;
		}
		p {
			display: none;

			&:first-of-type {
				display: block;
			}
		}
	}
}

.brand_item {
	display: flex;
	flex-wrap: wrap;

	&__image {
		flex: 0 0 rem(50);
		max-width: rem(50);
		margin-right: rem(14);
	}

	&__text {
		display: flex;
		flex-direction: column;

		span {
			font-size: rem(14);
			color: #9E9E9E;
			margin-bottom: rem(6);
		}

		b {
			margin-bottom: rem(6);
		}

		a {
			font-size: rem(14);
			color: #9E9D9D;
			text-decoration: underline;

			&:hover {
				color: var(--c-orange);
				text-decoration: none;
			}
		}
	}
}

.i_text_item {
	display: flex;
	// flex-wrap: wrap;

	&__image {
		margin-right: rem(8);
	}

	&__text {
		display: flex;
		flex-direction: column;

		p {
			margin-top: rem(6);
			font-size: rem(14);
		}
	}
}


.page-product {
	.header__control-burger {
		display: none;
	}
	.header__control-prev {
		display: block;
	}
}