.product_item {
	position: relative;
	border-radius: rem(8);
	overflow: hidden;
	border: rem(1) solid #DCDCDC;

	display: flex;
	flex-direction: column;

	padding: rem(16) rem(24);

	@include min540 {
		padding: rem(18);
	}

	&__sale {
		position: absolute;
		top: 0;
		left: 0;
		border-bottom-right-radius: rem(4);
		background-color: #F82929;
		padding: rem(6) rem(8);

		display: flex;
		align-items: center;

		.text {
			color: white;
			font-size: rem(8);
			font-weight: bold;
		}
	}

	&__category {
		position: absolute;
		top: rem(16);
		right: rem(24);
		border-radius: rem(15);
		background-color: rgba(196, 196, 196, 0.75);
		padding: rem(6) rem(8);

		display: flex;
		align-items: center;

		@include min540 {
			display: none;
		}

		.text {
			color: white;
			font-size: rem(12);
		}
	}

	&__tags {
		display: flex;
		flex-wrap: wrap;
		margin-bottom: rem(8);

		@include min540 {
			display: none;
		}

		a {
			border-radius: rem(3);
			border: rem(1) solid #323232;
			padding: rem(4) rem(8);

			color: #323232;

			margin-right: rem(8);
			font-size: rem(10);
			text-transform: lowercase;

			@include min540 {
				font-size: rem(9);
				margin-right: rem(4);
				padding: rem(3) rem(4);
			}

			&:last-child {
				margin-right: 0;
			}

			&:hover {
				border-color: var(--c-orange);
				color: var(--c-orange);
			}
		}
	}

	&__favorite {
		position: absolute;
		right: rem(24);
		bottom: rem(16);
		width: rem(40);
		height: rem(40);
		background-color: #F3F3F3;
		border-radius: 50%;

		display: flex;
		align-items: center;
		justify-content: center;

		@include min {
			display: none;
		}

		&:hover {
			svg {
				path {
					fill: var(--c-orange);
				}
			}
		}

		svg {
			path {
				transition: var(--transition);
				fill: #8D8C8C;
			}
		}
	}

	&__image {
		height: rem(185);
		margin-bottom: rem(14);

		display: flex;
		justify-content: center;
		align-items: center;

		@include min540 {
			height: rem(110);
			margin-bottom: rem(7);

			img {
				// width: rem(50);
				height: 100%;
				// object-fit: cover;
			}
		}
	}

	&__content {
		flex: 1;

		display: flex;
		flex-direction: column;

		&-buttons {
			margin-top: auto;

			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
		}
		

		.title {
			font-size: rem(14);
			font-weight: normal;
			line-height: 120%;
			margin-bottom: rem(14);

			@include min540 {
				font-size: rem(10);
				margin-bottom: rem(5);
			}
		}

		.price {
			font-size: rem(18);
			margin-bottom: rem(14);

			@include min540 {
				font-size: rem(12);
				margin-bottom: rem(2);
			}
		}

		.price_sale {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: rem(14);

			@include min540 {
				margin-bottom: rem(2);
				align-items: flex-end;
			}

			.first {
				text-decoration-line: line-through;
				font-size: rem(14);
				color: #9A9A9A;
				margin-right: rem(8);

				@include min540 {
					font-size: rem(12);
					color: #F82929;
				}
			}

			.last {
				font-size: rem(18);
				color: var(--c-orange);

				@include min540 {
					font-size: rem(10);
					color: #B1B1B1;
				}
			}
		}

		.button-orange {
			align-self: baseline;

			@include min540 {
				width: 100%;
			}
		}

		.button-green {
			@include min540 {
				width: 100%;
			}
		}
	}
}

.swiper-container {
	.product_item {
		height: 100%;
	}
}