.list_brand {
	display: flex;
	flex-wrap: wrap;

	@include min1050 {
		overflow-x: auto;
		width: 100%;
		flex-wrap: nowrap;
	}

	&__item {
		border-radius: rem(4);
		height: rem(160);
		width: calc((100% - 16px * 5) / 6);
		margin-right: rem(16);

		border: rem(1) solid #DCDCDC;

		display: flex;
		justify-content: center;
		align-items: center;

		transition: var(--transition);

		@include min1050 {
			flex: 0 0 rem(150);
			max-width: rem(150);

			height: rem(120);
		}

		&:hover {
			border-color: var(--c-orange);
		}

		&:last-child {
			margin-right: 0;
		}
	}
}