:root {
	--container: 1185px;
	--c-orange: #FE9C00;
	--c-orange-light: #FFE381;
	--c-dark-gray: #323232;
	--c-dark: #222222;
	--c-gray-light: #F3F3F3;
	--c-gray: #8D8C8C;
	--c-green: #52B636;
	--c-blue: #096193;

	--size-default: 14px;
	--f-default: 'Montserrat';

	--transition: 250ms ease;
}

* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
}

button, a {
	transition: var(--transition);
}

li {
	list-style: none;
}

body {
	position: relative;
	font-size: rem(16);
	font-family: var(--f-default);
	color: var(--c-dark);
	// height: 100%;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}


.container {
	max-width: var(--container);
	margin: 0 auto;

	@include min1200 {
		max-width: 100%;
		margin-left: 20px;
		margin-right: 20px;
	}
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}


.wrapper {
	display: flex;
	flex-wrap: wrap;

	margin-top: rem(28);
	margin-bottom: rem(42);
}