.list_category {
	display: flex;
	flex-wrap: wrap;

	margin-left: rem(-16);
	margin-bottom: rem(-16);

	@include min1050 {
		margin-left: 0;
		margin-bottom: 0;
	}

	&__item {
		margin-left: rem(16);
		margin-bottom: rem(16);

		border-radius: rem(8);
		background-color: #F4F4F4;

		// flex: 1;

		display: flex;
		align-items: center;

		padding: rem(30) rem(40);

		@include min768 {
			padding: rem(11) rem(24);
			margin-left: 0;
			margin-bottom: rem(10);

			height: rem(80);
		}

		&:hover {
			.text {
				color: var(--c-orange);
			}
		}

		&:first-child,
		&:nth-child(2),
		&:nth-child(3),
		&:nth-child(7),
		&:nth-child(8),
		&:nth-child(9),
		&:last-child {
			width: calc((100% - 48px) / 3);

			@include min1050 {
				width: calc((100% - 32px) / 2);
			}

			@include min768 {
				width: 100%;
			}
		}

		&:nth-child(4),
		&:nth-child(5) {
			width: calc((100% - 64px) / 4);

			@include min1050 {
				width: calc((100% - 32px) / 2);
			}

			@include min768 {
				width: 100%;
			}
		}

		&:nth-child(6) {
			width: calc((100% - 96px) / 6);
			padding: rem(30) rem(14);

			@include min1050 {
				width: calc((100% - 32px) / 2);
				padding: rem(30) rem(40);
			}

			@include min768 {
				padding: rem(11) rem(24);
				width: 100%;
			}

			.image {
				margin-right: rem(14);

				@include min1050 {
					margin-right: rem(30);
				}
			}
			
			img {
				
			}
		}

		.image {
			margin-right: rem(30);
			display: flex;
			align-items: center;
			justify-content: center;

			@include min768 {
				width: rem(68);
				height: rem(56);
				margin-right: rem(20);
			}
		}

		img {
			

			@include min768 {
				

				
				height: 100%;
			}
		}

		.text {
			transition: var(--transition);
			font-size: rem(24);

			@include min {
				font-size: rem(20);
			}

			@include min768 {
				font-size: rem(12);
			}
		}
	}
}