.modal_menu {

	@include max540 {
		display: none !important;
	}

	.modal__header {
		justify-content: space-between;

		margin-bottom: 0;

		&_first,
		&_parent,
		&_parent_parent {
			width: 100%;
			display: flex;
			justify-content: space-between;
		}

		&_first.hide,
		&_parent.hide,
		&_parent_parent.hide {
			display: none;
		}
	}

	.modal__close {
		position: relative;
		top: auto;
		right: auto;
	}

	.modal__search {
		svg {
			rect {
				stroke: var(--c-dark);
			}
		}
	}


	.modal__content {
		&_nav {
			position: relative;
			ul {
				display: flex;
				flex-direction: column;

				li {
					a {
						width: 100%;
						padding: rem(20);
						display: flex;
						align-items: center;
						justify-content: space-between;
						border-bottom: rem(1) solid #B1B1B1;

						svg {
							transition: var(--transition);
						}
					}

					.parent_menu {
						display: none;

						&_sub, &_sub_sub {
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							background-color: white;
							z-index: 10;

							display: none;
						}

						&_sub.active, &_sub_sub.active {
							display: block;
						}

						> li {
							> a {
								background-color: #F4F4F4;

								svg {
									transform: rotate(-90deg);
								}
							}
						}
					}
				}

				li.active {
					> a {

						box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.15);

						svg {
							transform: rotate(180deg);
						}
					}

					>.parent_menu {
						display: block;
					}
				}
			}
		}
	}

}